<template>
  <div class="home">
    <div class="banner_bg">
      <div class="banner_pc">
        <img src="../assets/images/banner_pc.png" alt="" width="100%">
      </div>
      <div class="banner_mobile d-none">
        <img src="../assets/images/banner_mobile.png" alt="" width="100%">
      </div>
    </div>
    <div class="content w-75 mx-auto mt-5">
      <div class="">
        <div class="d-flex justify-content-center mx-aut px-3 mb-3">
          <div class="border-10 bg-orange mr-3 py-3 w-100 point" data-toggle="modal" data-target="#qa">你問我答</div>
          <div class="border-10 bg-orange py-3 w-100 point" data-toggle="modal" data-target="#notice">注意事項</div>
        </div>
        <!-- qaModal -->
        <div class="modal fade" id="qa" tabindex="-1" role="dialog" aria-labelledby="qaTitle" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="qaTitle">你問我答</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                ...
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>
              </div>
            </div>
          </div>
        </div>
        <!-- noticeModal -->
        <div class="modal fade" id="notice" tabindex="-1" role="dialog" aria-labelledby="noticeTitle" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="noticeTitle">注意事項</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                ...
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>
              </div>
            </div>
          </div>
        </div>
        <p v-if="login" class="h6 mb-0 font-weight-bold mt-3">我要報名</p>
        <div v-else class="h6 mb-0 p-2 w-fit font-weight-bold mt-3 bg-red text-white border-10 mx-auto point" @click="gotologin">登入報名</div>
        <div class="steplist w-100">
          <div v-for="(item, index) in list" :key="index" class="stepitemALL">
            <!-- 登入顯示 -->
            <div class="position-relative mt-3 py-3 stepitem" v-if="login" @click="register(item)">
              <p class="mb-0 h7">{{ item.name }}</p>
              <div v-if="item.signup && item.status===''">
                <div class="stepitem-cover d-flex align-items-center justify-content-center">
                  <p class="mb-0 h7">已報名</p>
                </div>
              </div>
              <div v-else>
                <div class="stepitem-cover d-flex align-items-center justify-content-center" v-if="item.status!==''">
                  <p class="mb-0 h7">{{ item.status }}</p>
                </div>
              </div>
            </div>
            <!-- 未登入顯示 -->
            <div class="position-relative mt-3 py-3 stepitemdisable" v-else>
              <p class="mb-0 h7 text-black font-weight-bold">{{ item.name }}</p>
            </div>
          </div>
        </div>
        <p v-show="login" class="h6 mb-0 font-weight-bold mt-3 point" @click="logout">登出</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      list: [
        { name: '第一回合', year: 2020, month: 1, date: 1, deadyear: 2021, deadmonth: 1, deaddate: 9, signup: true, status: '' },
        { name: '第二回合', year: 2021, month: 1, date: 2, deadyear: 2021, deadmonth: 1, deaddate: 9, signup: false, status: '' },
        { name: '第三回合', year: 2021, month: 1, date: 2, deadyear: 2021, deadmonth: 1, deaddate: 9, signup: false, status: '' }
      ],
      login: sessionStorage.getItem('islogin') || false,
      date: new Date().getDate(),
      month: new Date().getMonth() + 1,
      year: new Date().getFullYear()
      // date: 14,
      // month: 4,
      // year: 2020
    }
  },
  created () {
    this.$nextTick(() => {
      this.dateShow()
    })
  },
  methods: {
    dateShow () {
      // 判斷日期 額滿(人工) 報名(API)
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].signup) {
          if (i === 0) {
            this.list[0].status = `贈點滿額...期待抽獎!`
          }
          if (i === 1) {
            this.list[1].status = `贈點滿額...期待抽獎!`
          }
          if (i === 2) {
            // this.list[2].status = `贈點滿額...期待抽獎!`
          }
        }
        if (this.list[i].year > this.year || (this.list[i].year >= this.year && this.list[i].month > this.month) || (this.list[i].year >= this.year && this.list[i].month >= this.month && this.list[i].date > this.date)) {
          this.list[i].status = `${this.list[i].month}/${this.list[i].date}開放報名`
        } else if (this.year > this.list[i].deadyear || (this.year >= this.list[i].deadyear && this.month > this.list[i].deadmonth) || (this.year >= this.list[i].deadyear && this.month >= this.list[i].deadmonth && this.date > this.list[i].deaddate)) {
          this.list[i].status = `回合結束`
        }
      }
    },
    gotologin () {
      this.login = true
      sessionStorage.setItem('islogin', this.login)
    },
    logout () {
      this.login = false
      sessionStorage.setItem('islogin', this.login)
    },
    register (item) {
      if (item.status !== '' || item.signup) return
      this.$router.push({name: 'Register', params: { userId: '123' }})
    },
    resizeHandler () {
      document.querySelector('.home').style.height = window.innerHeight + 'px'
      console.log(document.querySelector('.home').style.height)
    }
  }
}
</script>

<style lang="scss">
.home{
  background: #fff2e1;
  height: 100vh;
  overflow-y: scroll
}
</style>



